import 'bulma/css/bulma.css';
import cn from 'classnames';
import './App.scss';
import { useState } from 'react';

export const goodsFromServer = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

const SORT_FIELD_ALPHABET = 'alphabet';
const SORT_FIELD_LENGTH = 'length';
const REVERSE_FIELD = 'reversed';

function getPreparedGoods(goods, { sortField, reverseField }) {
  const preparedGoods = [...goods];

  if (sortField) {
    preparedGoods.sort((good1, good2) => {
      switch (sortField) {
        case SORT_FIELD_ALPHABET: {
          return good1.localeCompare(good2);
        }

        case SORT_FIELD_LENGTH: {
          return good1.length - good2.length;
        }

        default: {
          return 0;
        }
      }
    });
  }

  if (reverseField) {
    preparedGoods.reverse();
  }

  return preparedGoods;
}

export const App = () => {
  const [sortField, setSortField] = useState('');
  const [reverseField, setReverseField] = useState('');
  const goods = getPreparedGoods(goodsFromServer, { sortField, reverseField });

  return (
    <div className="section content">
      <div className="buttons">
        <button
          type="button"
          onClick={() => setSortField(SORT_FIELD_ALPHABET)}
          className={cn('button', 'is-info',
            {
              'is-light': sortField !== SORT_FIELD_ALPHABET,
            })}
        >
          Sort alphabetically
        </button>

        <button
          type="button"
          onClick={() => setSortField(SORT_FIELD_LENGTH)}
          className={cn('button', 'is-info',
            {
              'is-light': sortField !== SORT_FIELD_LENGTH,
            })}
        >
          Sort by length
        </button>

        {reverseField !== REVERSE_FIELD
          ? (
            <button
              type="button"
              onClick={() => setReverseField(REVERSE_FIELD)}
              className="button is-info is-light"
            >
              Reverse
            </button>
          )
          : (
            <button
              type="button"
              onClick={() => setReverseField('')}
              className={cn('button', 'is-info',
                {
                  'is-light': reverseField !== REVERSE_FIELD,
                })}
            >
              Reverse
            </button>
          )
        }

        {(sortField || reverseField) && (
          <button
            type="button"
            onClick={() => {
              setSortField('');
              setReverseField('');
            }}
            className="button is-danger is-light"
          >
            Reset
          </button>
        )}
      </div>

      <GoodList goods={goods} />
    </div>
  );
};

export const GoodList = ({ goods }) => (
  <ul>
    {goods.map(good => (
      <li
        data-cy="Good"
        key={good}
      >
        {good}
      </li>
    ))}
  </ul>
);
